import React from "react";
import './Section2.styles.scss';
import info from '../media/Statistics.webp';
import Lottie from './lottie.component'

const Section2 = () => {
  return (
    <section id="section2">
      <div id="sec2-container">
        <div id="sec2-column-left">
          <h1>
            THE
            <br />
            FUTURE
          </h1>

          <p>
            Our population is expected to reach 10
            <br />
            billion{" "}
            <span>
              in 2050 and we will no longer be
              <br />
              able to cover 50% of the animal-based
              <br />
              protein demand.
            </span>
          </p>

          <p>
            Either we have to start changing our
            <br />
            nutrition today or get started searching
            <br />
            for a new planet.
          </p>

          <p>
            <span>...we think the first one is easier.</span>
          </p>

          <img alt="statistics" src={info} />
        </div>

        <div id="sec2-column-right">
            <h1>PLANT OR<br/>BEEF: 1g vs 1g</h1>
            <Lottie
            lottie_json="https://assets8.lottiefiles.com/packages/lf20_gbXQhT.json"
            lottie_speed="1.5"
            
            />

            <Lottie
            lottie_json="https://assets1.lottiefiles.com/packages/lf20_cpWxcv.json"
            lottie_speed="1.5"
            
            />
            <Lottie
            lottie_json="https://assets3.lottiefiles.com/packages/lf20_WDBBYo.json"
            lottie_speed="1.5"
            
            />

<Lottie
            lottie_json="https://assets6.lottiefiles.com/packages/lf20_N2J1OA.json"
            lottie_speed="1.5"
            
            />



        
        </div>
      </div>
    </section>
  );
};

export default Section2;
