import React from "react";
import "./Section1.styles.scss";
import powder from "../media/protein_powder.png";
import leftcocoa from "../media/left_cocoa_beans.png";
import rightcocoa from "../media/right_cocoa_beans.png";
import fruit from "../media/Cocoa_Fruit.png";

const Section1 = () => {
  return (
    <section id="section1">
      <div id="section1-container" className="container">
        <div id="sec1-column-left" className="sec1-column">
          <h1>
            A STARTUP
            <br />
            FOCUSED ON
            <br />
            PLANT-BASED
            <br />
            FITNESS FOOD
            <br />
          </h1>

          <form>
            <div class="form-container">
              <input placeholder="YOUR-EMAIL"></input>
              <button>SUBSCRIBE</button>
            </div>
          </form>
        </div>

        <div id="sec1-column-right" className="sec1-column">
          <img src={powder} alt="powder" />
          <img src={fruit} alt="fruit" />
          <img src={leftcocoa} alt="left-cocoa" />
          <img src={rightcocoa} alt="right-cocoa" />
        </div>
      </div>
    </section>
  );
};

export default Section1;
